import React, { Component } from 'react';
import styled from 'styled-components';
import CopyIconBtn from 'react-icons/lib/md/content-copy';
import copy from 'copy-to-clipboard';

const ContentContainer = styled.div`
	display: grid;
	grid-gap: 10px 10px;
	grid-template-columns: ${ window.innerWidth > 960  ? '140px 420px 20px' : '320px' };

	>svg {
		display: ${ window.innerWidth > 960 ? 'inline' : 'none' };
		color: #666;
		font-size: 14px;
		margin: 20px 20px 15px 20px;
		&:hover {
			color: #ccc;
		}
	}
`
const DataType = styled.div`
	font-size: 14px;
	font-weight: bolder;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
`
const Output = styled.div`
	font-size: 14px;
	margin-left: ${ window.innerWidth > 400 ? (window.innerWidth > 960 ? 0 : 40) : 30 }px !important;
	margin-right: ${ window.innerWidth > 480 ? 'none' : '30px' };
	text-align: left;
`
const ResponsiveNextLine = styled.span`
  display: ${ window.innerWidth > 530 ? 'none' : 'block' }
  &:after {
    content: ' ';
  }
`

export default () => (
	<ContentContainer>
	<DataType>
	2011 - 2014
	</DataType>
	<Output>
	The Community College at Lingnan University<br/>
	Higher-Diploma in Computer Studies
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('The Community College at Lingnan University - Higher-Diploma in Computer Studies');
	}} />
	<ResponsiveNextLine />
	<DataType>
	2005 - 2010
	</DataType>
	<Output>
	Yan Oi Tong Chan Wong Suk Fong Memorial Secondary School<br/>
	Form Five
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('Yan Oi Tong Chan Wong Suk Fong Memorial Secondary School - Form Five');
	}} />
	</ContentContainer>
)



