import React, { Component } from 'react';
import styled from 'styled-components';
import CopyIconBtn from 'react-icons/lib/md/content-copy';
import copy from 'copy-to-clipboard';

const ContentContainer = styled.div`
	display: grid;
	grid-template-columns: ${ window.innerWidth>960 ? '120px 80px 20px' : '120px 80px' };
	>svg {
		display: ${ window.innerWidth>960 ? 'inline' : 'none' };
		color: #666;
		font-size: 14px;
		margin: 15px 20px 0px 20px;
		&:hover {
			color: #ccc;
		}
	}
`
const DataType = styled.div`
	font-size: 14px;
	font-weight: bolder;
	margin: 10px 20px 0px 30px;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
`
const Output = styled.div`
	font-size: 14px;
	margin: 10px 20px 0px 20px;
	text-align: left;
`

export default () => (
	<ContentContainer>
	<DataType>
	English
	</DataType>
	<Output>
	fluent
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('English - fluent');
	}} />
	<DataType>
	Cantonese
	</DataType>
	<Output>
	fluent
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('Cantonese - fluent');
	}} />
	<DataType>
	Mandarin
	</DataType>
	<Output>
	fluent
	</Output>
	<CopyIconBtn onClick={ () => {
		copy('Mandarin - fluent');
	}} />
	</ContentContainer>

)



