import React, { Component } from 'react';
import styled from 'styled-components';
import { BrowserRouter as Router, Route, Switch, withRouter, HashRouter } from 'react-router-dom';
import Info from './info';
import store from '../store';
import Detail from './contentFolder/detail';
import Edu from './contentFolder/edu';
import Work from './contentFolder/work';
import Lang from './contentFolder/lang';
import Skill from './contentFolder/skill';
import Home from './contentFolder/home';
import Unauth from './contentFolder/unauthorized';
import RightBtn from 'react-icons/lib/fa/angle-right';
import LeftBtn from 'react-icons/lib/fa/angle-left';
import Footer from './footer';

const CurPage = styled.div`
	color: #ccc;
	top: ${ window.innerWidth>960 ? 130 : 100 }px;
	position: absolute;
	background: #333;
	width: ${ window.innerWidth>960 ? 960 : window.innerWidth }px;
	min-height: ${ window.innerWidth>960 ? window.innerHeight-160 : window.innerHeight-130 }px
	padding-top: 30px;
	overflow: scroll;

`
const MainSteam = styled.div`
	font-size: 24px;
	margin: 0px 0px 15px 40px;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
	width: ${ window.innerWidth>960 ? 960*0.6 : window.innerWidth *0.8 }px;
	border-bottom: solid 1px #666;
	padding-bottom: 5px;
`

const Title = styled.span`
	font-weight: bold;
	font-size: 24px;
`
const Output = props => {
	switch (props.toRender) {
		case 'detail':
			return (
				<Detail />
			)
			break;
		case 'edu':
			return (
				<Edu />
			)
			break;
		case 'work':
			return (
				<Work />
			)
			break;
		case 'lang':
			return (
				<Lang />
			)
			break;
		case 'skill':
			return (
				<Skill />
			)
			break;
		case 'home':
			return (
				<Home />
			)
				break;
		default:
			return (
				<Unauth />
			)
	}
}

const NextBtn = styled(RightBtn)`
		right: -12px;
		top: ${ window.innerHeight*0.5 - 10 }px;
		color: #444;
		font-size: 48px;
		position: fixed;
`
const PrevBtn = styled(LeftBtn)`
		left: -12px;
		top: ${ window.innerHeight*0.5 - 10 }px;
		color: #444;
		font-size: 48px;
		position: fixed;
`
class PageInfo extends Component {

	constructor(props){
		super(props);
		window.innerHeight>650 && window.innerWidth>960 ? window.addEventListener('wheel', this.scroll.bind(this)) : '';
	}
	scroll(e) {
		let curIndex = this.props.pageGo.index;
		let nextIndex = e.deltaY>0 ? ( curIndex < 5 ? curIndex + 1 : curIndex ) : (curIndex > 0 ? curIndex - 1 : curIndex );
		if(curIndex !== nextIndex) {
			let findInfo = Info.find( x => x.index === nextIndex );
		  store.dispatch( {type:'route', data: findInfo.path, index: nextIndex} );
		  this.props.history.push( '/'+ findInfo.path);
		}
	}
	pageDec(){
		if(this.props.pageGo.index >0) {
			let index = this.props.pageGo.index -1;
			let findInfo = Info.find( x => x.index === index );
			store.dispatch( {type:'route', data: findInfo.path, index: index} );
			this.props.history.push( '/'+ findInfo.path);
		}
	}
	pageInc(){
		if(this.props.pageGo.index <5) {
			let index = this.props.pageGo.index +1;
			let findInfo = Info.find( x => x.index === index );
			store.dispatch( {type:'route', data: findInfo.path, index: index} );
			this.props.history.push( '/'+ findInfo.path);
		}
	}
	hideMenu(){
		window.innerWidth>960 ? '' :  store.dispatch( {type:'menu', toShowBool: false, data: this.props.pageGo.path, index: this.props.pageGo.index } );
	}
	render() {
		this.props.match.path === '/' ? this.props.history.push( '/home') : '';
		this.props.pageGo ? store.dispatch( {type:'route', data: this.props.pageGo.path, index: this.props.pageGo.index} ) : console.log('props is undefined');
		window.scrollTo(0,0);
		navigator.userAgent.match('Firefox') ? document.cancelFullScreen() : document.exitFullscreen();
		return (
			<CurPage onClick={this.hideMenu.bind(this)}>
				<MainSteam>
					{this.props.pageGo === undefined ? 'PAGE NOT FOUND!' : this.props.pageGo.object }
				</MainSteam>
					<Output toRender={this.props.pageGo? this.props.pageGo.path : 'mo'}/>
				<PrevBtn onClick={this.pageDec.bind(this, -1)} />
				<NextBtn onClick={this.pageInc.bind(this, 1)} />
				<Footer />
			</CurPage>
		)
	}
}
const PageChange = withRouter(PageInfo);

export default () => {
	return (
		<Switch>
			<Route exact path="/" render={ ({ match }) => (
				<PageChange pageGo={ Info.find( x => x.path === 'home' ) } />
			)}/>
			<Route path="/:id" render={ ({ match }) => (
				<PageChange pageGo={ Info.find( x => x.path === match.params.id.trim().toLowerCase() ) } />
			)
			}/>
		</Switch>
	)
}



