import React, { Component } from 'react';
import styled from 'styled-components';
import { BrowserRouter as Router, Link } from 'react-router-dom';
import pages from './info';
import Content from './content';
import { connect } from 'react-redux';
import MenuIcon from 'react-icons/lib/go/three-bars';
import store from '../store';

const HeadContainer = styled.div`
  background: #2e3e49;
  position: absolute;
  height: ${ window.innerWidth>960 ? 130 : 100 }px;
  width: ${ window.innerWidth>960 ? 960 : window.innerWidth }px;
  top: 0px;
  z-index: 3;
`
const MenuContainer = styled.div`
	margin-left: 20px;
`
const SimpleMenu = styled.span`
  background: none;
  border: none;
  display: ${ window.innerWidth>960 ? 'inline' : 'none' };
	>a {
		color: ${ props => props.isActive ? '#ccc' : '#666' };
		margin: 20px;
		font-size: 14px;
		font-weight: bold;
		text-decoration: none;
		&:hover{
			color: ${ props => props.isActive ? '#ccc' : '#999' };
		}
	}
`
const MobileMenu = styled.span`
  display: none;
  >a {
    color: ${ props => props.isActive ? '#ccc' : '#888' };
    font-size: 14px;
    text-decoration: none;
    font-weight: bold;
    &:hover{
      color: ${ props => props.isActive ? '#ccc' : '#aaa' };
    }
  }
`


const NoStyledLink = styled(Link)`
    text-decoration: none;
`

const Name = styled.h1`
	color: #ccc;
	margin-bottom: 2px;
	margin-left: ${window.innerWidth<=360? '30px' : '40px'} !important;
`
const Cv = styled.h4`
	color: #ccc
	margin-top: 0px;
	margin-left: ${window.innerWidth<=360? '30px' : '40px'} !important;
`

const SMenuIcon = styled(MenuIcon)`
  position: absolute;
  top:25px;
  right:15px;
`


const MenuBtn = styled.div`
  position: fixed;
  display: ${ window.innerWidth<960 ? 'inline' : ( props => props.isActive ? 'inline': 'none') };
  color: #ccc;
  top: 5px;
  right: 5px;
  padding: 20px;
  font-size: 36px;
  z-index: 2;
  width: ${ props => props.styleProps ? 200 : 60 }px;
  height: ${ props => props.styleProps ? 260 : 60 }px;
  border: ${ props => props.styleProps ? 'solid 5px #22313F' : 'none' };
  background: ${ props => props.styleProps ? '#22313F' : 'none' };
  border-radius: 10px;
  >span {
    display: ${ props => props.styleProps ? 'block' : 'none'};
  }
  >svg {
    position: absolute;
    top: ${ props => props.styleProps ? '20px' : 'none'};
    right: ${ props => props.styleProps ? '10px' : 'none'};
  }
`


class Options extends Component {

  isDisplay() {
    store.dispatch( {type:'menu', toShowBool: this.props.toShowBool ? false : true, data: this.props.data, index: this.props.index } );
  }

  render() {
    const simpleOutput = [];
    const mobileOuput = [];
    pages.forEach( ( x, cur ) => {
      if(window.innerWidth>960){
        simpleOutput.push(
          <SimpleMenu key={cur} isActive={ x.path===this.props.data ? true : false } >
            <Link to={x.path}>
              {x.object}
            </Link>
          </SimpleMenu>
        )
      } else {
        mobileOuput.push(
          <MobileMenu key={cur} isActive={ x.path===this.props.data ? true : false }>
            <Link to={x.path}>
              {x.object}
            </Link>
          </MobileMenu>
        )
      }
    })
    return (
      <MenuContainer>
        <MenuBtn onClick={this.isDisplay.bind(this)} styleProps={this.props.toShowBool}>
          <SMenuIcon/>
          {mobileOuput}
        </MenuBtn>
        {simpleOutput}
      </MenuContainer>
    );
  }
}

const mapStateToProps = state => {
  return { index: state.index, data: state.data, toShowBool: state.toShowBool,  }
}

const Poptions = connect(mapStateToProps)(Options);

export default () => (
  <HeadContainer>
    <NoStyledLink to='/'>
    <Name>Man Ho Fung </Name>
    <Cv>Curriclum Vitae</Cv>
    </NoStyledLink>
    <Poptions />
  </HeadContainer>
)



